body{
  margin: 0;
  padding: 0;
 overflow: hidden;

}

.stage{
width: 100%;
height:750px;
background-color: #f9f9f9;

}
.img-sec{
width: 100%;
height:100%;
background-color:#eeeeee;
-webkit-transition: all 0.6s ease-in-out;
-o-transition: all 0.6s ease-in-out;
transition: all 0.6s ease-in-out;


}
.img-figure{
  position: absolute;
  border: 1px solid rgb(245, 245, 245);
  width:300px;
  height:350px; 
  background-color: #f9f9f9;
  top: 0;
 
  -webkit-transition: all 0.6s ease-in-out;
 
  -o-transition: all 0.6s ease-in-out;
 
  transition: all 0.6s ease-in-out;
}
.img-figure img{
  position: relative;
  border: 1px solid rgb(245, 245, 245);
  width:240px;
  height:240px; 
  margin: 30px 30px  5px 30px ;
 
}
.img-title{
  font-size:16px;
  text-align: center;
}
.img-back{
  display:inline;
}




